.main_start {
    width: 100%;
    box-sizing: border-box;
    padding: 20px 20px;
    background-color: #94d4b1;
    .main_start_center {
        margin: 0 auto;
        display: flex;
        flex-direction: row;
        padding: 130px 40px 10px 40px;
        .left {
            width: 50%;
            font-size: 24px;
            font-weight: bold;
            padding: 100px 15px;
            }
        .right {
            font-size: 18px;
            .title {
                font-weight: 700;
            }
        }
    }
}