.news {
    width: 100%;

    .main {
        width: 100%;

        .main_start {
            background-color: #FF8c82;
            .left {
                width: 35%;
            }

        }

        .main_content {
            width: 100%;
            margin: 0 auto;
            padding: 30px;
            box-sizing: border-box;

        
            .content_top {
                display: flex;
                flex-direction: column;
           
                .ctitle {
                    height: 100px;
                    line-height: 100px;
                    font-size: 20px;
                    font-weight: 700;
                }

                .content_expend {
                    display: flex;
                    flex-direction: row;
                    justify-content: space-around;
                    margin-bottom: 30px;
                    .content_expend_item {
                        width: 25%;
                        height: 450px;
                        border: 1px solid #ccc;
                        border-radius: 16px;
                        box-shadow: 10px 10px 5px #a1a1a1;
                    }
                }
            }
        }

        .content_bottom {
          
            margin: 0 auto;
            padding: 10px 0px;
            margin-bottom: 30px;
            .ctitle {
                height: 100px;
                line-height: 100px;
                font-size: 20px;
                font-weight: 700;
            }
         

            .footer_item {
                height: 300px;
                border: 1px solid #ccc;
                border-radius: 16px;
                box-shadow: 10px 10px 5px #888888;
                margin-top: 50px;
                padding: 30px;
                font-size: 20px;
                background-color: rgb(221, 221, 221);
            }
        }
    }
 
}